import React, { useState, useEffect } from 'react';
import {
  Grid,
  TextField,
  Typography,
  Paper,
  Box,
  Alert
} from '@mui/material';
import {
  calculateSPECjbb2005,
  calculateTransactionsPerSecond,
  calculateServerResources,
  BASELINE_SINGLE_CORE
} from '../../services/performanceCalculator';

const STORAGE_KEY = 'specjbbCalculatorData';

export default function SPECjbbCalculator({ onResultChange }) {
  const [inputs, setInputs] = useState(() => {
    // localStorage
    const savedData = localStorage.getItem(STORAGE_KEY);
    return savedData ? JSON.parse(savedData) : {
      totalUsers: 158200, // 
      peakConcurrencyRate: 0.001, // 0.1%
      tasksPerConcurrentUser: 2, // 
      
      // 
      requestsPerTransaction: 4, // 
      transactionsPerRequest: 5, // 
      
      // SPECjbb2005
      peakValuePerTransaction: 1500, // SPECjbb2005
      redundancyCapacity: 0.3, //  (30%)
      nonJavaResourcePercentage: 0 // Java
    };
  });

  const handleInputChange = (event) => {
    const { name, value } = event.target;
    setInputs(prev => ({
      ...prev,
      [name]: parseFloat(value) || 0
    }));
  };

  // 
  const calculatePeakConcurrentUsers = () => {
    return inputs.totalUsers * inputs.peakConcurrencyRate;
  };

  // 
  const calculatePeakConcurrentTransactions = () => {
    const peakConcurrentUsers = calculatePeakConcurrentUsers();
    return peakConcurrentUsers * inputs.tasksPerConcurrentUser;
  };

  useEffect(() => {
    // localStorage
    localStorage.setItem(STORAGE_KEY, JSON.stringify(inputs));

    const peakConcurrentTransactions = calculatePeakConcurrentTransactions();
    
    // 
    const transactionsPerSecond = calculateTransactionsPerSecond({
      peakConcurrentTransactions,
      requestsPerTransaction: inputs.requestsPerTransaction,
      transactionsPerRequest: inputs.transactionsPerRequest
    });

    // SPECjbb2005
    const specjbbResult = calculateSPECjbb2005({
      transactionsPerSecond,
      peakValuePerTransaction: inputs.peakValuePerTransaction,
      redundancyCapacity: inputs.redundancyCapacity,
      nonJavaResourcePercentage: inputs.nonJavaResourcePercentage
    });

    // 
    const { appServerCores } = calculateServerResources({
      specjbbValue: specjbbResult,
      tpccValue: 0, // SPECjbb
      baselineSingleCore: BASELINE_SINGLE_CORE
    });

    const finalResult = {
      peakConcurrentUsers: calculatePeakConcurrentUsers(),
      peakConcurrentTransactions,
      transactionsPerSecond,
      specjbbValue: specjbbResult,
      appServerCores,
      explanation: generateExplanation(transactionsPerSecond, specjbbResult, appServerCores, inputs)
    };

    onResultChange(finalResult);
  }, [inputs, onResultChange]);

  const generateExplanation = (transactionsPerSecond, specjbbResult, appServerCores, params) => {
    const peakConcurrentUsers = calculatePeakConcurrentUsers();
    const peakConcurrentTransactions = calculatePeakConcurrentTransactions();
    
    return `WEBWEB

SPECjbb2005SPECjbb2005 = A  B /1 - C - D

A
BSPECjbb2005
C
DJava


1. 
   - ${params.totalUsers}
   - ${(params.peakConcurrencyRate * 100).toFixed(3)}%
   -  = ${params.totalUsers}  ${(params.peakConcurrencyRate * 100).toFixed(3)}% = ${peakConcurrentUsers.toFixed(1)}
   - ${params.tasksPerConcurrentUser}
   -  = ${peakConcurrentUsers.toFixed(1)}  ${params.tasksPerConcurrentUser} = ${peakConcurrentTransactions.toFixed(1)}

2. 
   - ${params.requestsPerTransaction}
   - ${params.transactionsPerRequest}
   -  = ${peakConcurrentTransactions.toFixed(1)}  ${params.requestsPerTransaction}  ${params.transactionsPerRequest} = ${transactionsPerSecond}

3. SPECjbb2005
   - SPECjbb2005${params.peakValuePerTransaction}
   - ${params.redundancyCapacity * 100}%
   - Java${params.nonJavaResourcePercentage * 100}%
   
SPECjbb2005 = ${transactionsPerSecond}  ${params.peakValuePerTransaction} / (1 - ${params.redundancyCapacity} - ${params.nonJavaResourcePercentage}) = ${specjbbResult.toLocaleString()}

4. 
   - SPECjbb2005${BASELINE_SINGLE_CORE.specjbb}
   - 10%
   -  = ${specjbbResult.toLocaleString()}  ${BASELINE_SINGLE_CORE.specjbb}  (1 - 0.1) = ${appServerCores}`;
  };

  return (
    <Box>
      <Typography variant="h6" gutterBottom>
        SPECjbb2005
      </Typography>

      <Grid container spacing={3}>
        <Grid item xs={12} md={6}>
          <Paper elevation={2} sx={{ p: 2, position: 'relative' }}>
            <Typography variant="subtitle1" gutterBottom>
              
            </Typography>
            <Box 
              sx={{ 
                position: 'absolute', 
                top: 16, 
                right: 16, 
                bgcolor: 'primary.main',
                color: 'white',
                px: 2,
                py: 1,
                borderRadius: 1,
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center'
              }}
            >
              <Typography variant="caption" sx={{ mb: 0.5 }}>
                
              </Typography>
              <Typography variant="h6" component="div">
                {(() => {
                  const peakConcurrentTransactions = calculatePeakConcurrentTransactions();
                  const transactionsPerSecond = calculateTransactionsPerSecond({
                    peakConcurrentTransactions,
                    requestsPerTransaction: inputs.requestsPerTransaction,
                    transactionsPerRequest: inputs.transactionsPerRequest
                  });
                  const specjbbResult = calculateSPECjbb2005({
                    transactionsPerSecond,
                    peakValuePerTransaction: inputs.peakValuePerTransaction,
                    redundancyCapacity: inputs.redundancyCapacity,
                    nonJavaResourcePercentage: inputs.nonJavaResourcePercentage
                  });
                  const { appServerCores } = calculateServerResources({
                    specjbbValue: specjbbResult,
                    tpccValue: 0,
                    baselineSingleCore: BASELINE_SINGLE_CORE
                  });
                  return `${appServerCores}`;
                })()}
              </Typography>
            </Box>
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  label=""
                  name="totalUsers"
                  type="number"
                  value={inputs.totalUsers}
                  onChange={handleInputChange}
                  helperText=""
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  label=""
                  name="peakConcurrencyRate"
                  type="number"
                  value={inputs.peakConcurrencyRate}
                  onChange={handleInputChange}
                  helperText="0-10.0010.1%"
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  label=""
                  name="tasksPerConcurrentUser"
                  type="number"
                  value={inputs.tasksPerConcurrentUser}
                  onChange={handleInputChange}
                  helperText=""
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  disabled
                  label=""
                  value={calculatePeakConcurrentUsers().toFixed(1)}
                  helperText="  "
                />
              </Grid>
            </Grid>
          </Paper>
        </Grid>

        <Grid item xs={12} md={6}>
          <Paper elevation={2} sx={{ p: 2 }}>
            <Typography variant="subtitle1" gutterBottom>
              
            </Typography>
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  label=""
                  name="requestsPerTransaction"
                  type="number"
                  value={inputs.requestsPerTransaction}
                  onChange={handleInputChange}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  label=""
                  name="transactionsPerRequest"
                  type="number"
                  value={inputs.transactionsPerRequest}
                  onChange={handleInputChange}
                />
              </Grid>
            </Grid>
          </Paper>
        </Grid>

        <Grid item xs={12}>
          <Paper elevation={2} sx={{ p: 2 }}>
            <Typography variant="subtitle1" gutterBottom>
              SPECjbb2005
            </Typography>
            <Grid container spacing={2}>
              <Grid item xs={12} md={4}>
                <TextField
                  fullWidth
                  label="SPECjbb2005"
                  name="peakValuePerTransaction"
                  type="number"
                  value={inputs.peakValuePerTransaction}
                  onChange={handleInputChange}
                  helperText="bops"
                />
              </Grid>
              <Grid item xs={12} md={4}>
                <TextField
                  fullWidth
                  label=""
                  name="redundancyCapacity"
                  type="number"
                  value={inputs.redundancyCapacity}
                  onChange={handleInputChange}
                  helperText="0-10.330%"
                />
              </Grid>
              <Grid item xs={12} md={4}>
                <TextField
                  fullWidth
                  label="Java"
                  name="nonJavaResourcePercentage"
                  type="number"
                  value={inputs.nonJavaResourcePercentage}
                  onChange={handleInputChange}
                  helperText="0-10.220%"
                />
              </Grid>
            </Grid>
          </Paper>
        </Grid>
      </Grid>

      {parseFloat(inputs.redundancyCapacity) + parseFloat(inputs.nonJavaResourcePercentage) >= 100 && (
        <Box mt={2}>
          <Alert severity="error">
            Java100%
          </Alert>
        </Box>
      )}
    </Box>
  );
} 